/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import { LangCompilerSelect } from '@/components/compiler/lang-compiler-select';
import { Compiler } from '@/types/api/compiler';
import { Language } from '@/types/api/language';
import { Editor } from '@monaco-editor/react';
import { useEffect, useRef, useState } from 'react';

export default function Page() {
  function handleEditorDidMount(editor: any) {
    editorRef.current = editor;
    editor.focus();
    editor.RenderMinimap = 0;
  }
  function handleOnChange(value: string | undefined) {
    if (value) setSourceCode(value);
  }

  useEffect(() => {
    async function fetchLanguages() {
      const res = await fetch('http://localhost:3001/langs');
      const json: Language[] = await res.json();
      setLanguages(json);
      setLanguage(json[0]);
      setCompiler(json[0].compilers[0]);
    }
    fetchLanguages();
  }, []);

  const [sourceCode, setSourceCode] = useState('');
  const [languages, setLanguages] = useState<Language[]>([]);
  const [language, setLanguage] = useState<Language>();
  const [compiler, setCompiler] = useState<Compiler>();
  const editorRef = useRef(null);

  return (
    <div className='flex h-full w-full p-5'>
      <div className='h-full w-1/2 overflow-clip rounded-2xl'>
        <Editor
          onMount={handleEditorDidMount}
          onChange={handleOnChange}
          defaultLanguage={language?.monaco_name}
          defaultValue='console.log("Hello, world!")'
          value={sourceCode}
          options={{
            wordWrap: 'on',
            fontSize: 20,
            minimap: {
              enabled: false,
            },
          }}
        />
      </div>
      <div className='h-full w-1/2'>
        <LangCompilerSelect
          compiler={compiler}
          lang={language}
          langs={languages}
          setCompiler={setCompiler}
          setLang={setLanguage}
        />
      </div>
    </div>
  );
}
