import { Compiler } from '@/types/api/compiler';
import { Language } from '@/types/api/language';
import { Dispatch, SetStateAction } from 'react';
import {
  Select,
  SelectContent,
  SelectGroup,
  SelectItem,
  SelectLabel,
  SelectTrigger,
  SelectValue,
} from '../ui/select';

export function LangCompilerSelect({
  langs,
  lang,
  compiler,
  setLang,
  setCompiler,
}: {
  langs: Language[];
  lang: Language;
  compiler: Compiler;
  setLang(lang: Language): Dispatch<SetStateAction<Language>>;
  setCompiler(compiler: Compiler): Dispatch<SetStateAction<Compiler>>;
}) {
  return (
    <div className='flex items-center justify-center'>
      <Select
        onValueChange={v => {
          setLang(langs.find(l => l.name === v) ?? langs[0]);
        }}
      >
        <SelectTrigger className='w-[180px]'>
          <SelectValue defaultValue={lang.name} />
        </SelectTrigger>
        <SelectContent>
          <SelectGroup>
            <SelectLabel>Languages</SelectLabel>
            {langs.map(l => (
              <SelectItem key={l.id} value={l.name}>
                {l.name}
              </SelectItem>
            ))}
          </SelectGroup>
        </SelectContent>
      </Select>
      <Select
        onValueChange={v => {
          setCompiler(
            lang.compilers.find(c => c.docker_image_name === v) ??
              lang.compilers[0],
          );
        }}
      >
        <SelectTrigger className='w-[180px]'>
          <SelectValue defaultValue={compiler.docker_image_name} />
        </SelectTrigger>
        <SelectContent>
          <SelectGroup>
            <SelectLabel>Compilers</SelectLabel>
            {lang.compilers.map(c => (
              <SelectItem key={c.id} value={c.docker_image_name}>
                {c.docker_image_name}
              </SelectItem>
            ))}
          </SelectGroup>
        </SelectContent>
      </Select>
    </div>
  );
}
